<template>
    <div class="balance">
        <div class="wrapper">
        <div class="balance-block">
            <label class="balance-title">{{$localizeFilter('balance')}}</label>
            <div class="amount-block">
                <label class="balance-amount">{{ balance ?? '0' }}</label>
                <label class="balance-currency">dream</label>
            </div>
        </div>
        <button class="dark-button fill" v-if="type=='balance'" @click="fillBalance">{{$localizeFilter('buy_tar')}}</button>
        </div>
        <div class="buttons-block">
            <button class="dark-button" @click="handleClick">{{type=='balance' ? $localizeFilter('withdrawal_applications') : $localizeFilter('fill_sigma')}}</button>
            <!-- <button class="dark-button" v-if="type=='balance'" @click="withdrawFromBalance">{{type=='balance' ? $localizeFilter('withdrawal') : $localizeFilter('withdraw_from_sigma')}}</button> -->
            <button class="dark-button" @click="withdrawFromBalance">{{type=='balance' ? $localizeFilter('withdrawal') : $localizeFilter('withdraw_from_sigma')}}</button>
            <button class="dark-button" @click="transferFromBalance">{{type=='balance' ? $localizeFilter('transfer') : $localizeFilter('withdraw_from_sigma')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        type: {
            type: String,
            default: 'balance' //  'balance', 'sigma'
        }
    },
    data: ()=>({
        balance: 0,
        walletBalance: 0
    }),
    setup() {
        
    },
    methods: {
        fillBalance() {
            this.$emit('fillBalance')
        },
        withdrawFromBalance() {
            this.$emit('withdrawFromBalance')
        },
        showWithdrawalRequests() {
            if (this.type=='balance') {
                this.$emit('showWithdrawalRequests')
            }
        },
        transferFromBalance() {
            this.$emit('transferFromBalance')
        },
        handleClick() {
            if (this.type=='balance') {
                this.showWithdrawalRequests()
            } else {
                this.fillBalance()
            }
        },
        async loadData(){
            switch (this.type) {
            case 'balance':
                try {
                    const data = await this.$store.dispatch('getBalance')
                    this.balance = data.availableBalance.toFixed(2)
                    this.$emit('setBalance', this.balance)
                } catch(e) {
                    this.$error(e)
                } 
                break
            case 'sigma':
                try {
                    const data = await this.$store.dispatch('getSigmaBalance')
                    const {balance: walletBalance} = await this.$store.dispatch('getBalance')
                    this.balance = data.balance.toFixed(2)
                    this.walletBalance = walletBalance.toFixed(2)
                    this.$emit('setBalance', {balance: this.balance, walletBalance: this.walletBalance})
                } catch(e) {
                    this.$error(e)
                } 
            }
        }
    },
    
     async mounted() {
        await this.loadData()

    }
})
</script>

