<template>
    <div class="wallet">
        <div class="header-block">
            <p class="cabinet-header">{{$localizeFilter('wallet')}}</p>
            <p class="cabinet-text">{{$localizeFilter('wallet_text')}}</p>
        </div>
        <Balance class="balance" :type="'balance'" @fillBalance="fillBalance" @withdrawFromBalance="withdrawFromBalance" @transferFromBalance="transferFromBalance" @showWithdrawalRequests="showWithdrawalRequests"/>
        <Operations class="operations" :type="'wallet'" :showOptions="true"/>
        <Modal v-if="showModal" :type="'wallet'" :actionType="actionType" :step="actionStep" @updateModal="updateModal" @closeModal="closeModal"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Balance from '@/components/Balance.vue'
import Operations from '@/components/Operations.vue'
import Modal from "@/components/modals/Modal.vue"

export default defineComponent({
    components: {
        Balance, Operations, Modal
    },
    setup() {
        
    },
    data: () => ({
      showModal: false,
      actionType: null,
      actionStep: 1
    }),
    mounted() {
        this.$emit('mountedChild', 'Wallet');
    },
    methods: {
      closeModal() {
        this.showModal = false
        this.actionStep = 1
      },
    fillBalance() {
        // if(location.origin.includes('localhost') || location.origin.includes('next')) {
            this.actionType = 'replenish'
            this.actionStep = 1
            this.showModal = true
        // }
    },
    withdrawFromBalance() {
        // if(location.origin.includes('localhost') || location.origin.includes('next')) {
            this.actionType = 'withdrawal'
            this.showModal = true
        // }
    },
    transferFromBalance() {
        // if(location.origin.includes('localhost') || location.origin.includes('next')) {
            console.log('transter')
            this.actionType = 'transfer'
            this.showModal = true
        // }
    },
    showWithdrawalRequests() {
        this.$router.push('/cabinet/withdrawalRequests')
    },
    updateModal(step) {
        this.actionStep=step
    } 
    }
})
</script>

