<template>
<div class="place-for-table">
  <div class="table-options-block">
      <TableOptions class="table-options" v-if="showOptions" :name="$localizeFilter('operations')" :options="types" :prefix="'operations_'" @selectOption='selectOption'/>
  </div>
  <div class="custom-table"  v-if="showTable">
    <div class="custom-table-header">
      <div class="custom-table-row light header">
        <div class="custom-data-cell header">{{layoutAsMobile ? $localizeFilter('operation_name_amount') : $localizeFilter('operation_name')}}</div>
        <div class="custom-data-cell header" v-if="!layoutAsMobile">{{$localizeFilter('operation_amount')}}</div>
        <div class="custom-data-cell header">{{$localizeFilter('operation_date')}}</div>
      </div>
    </div>

    <div class="custom-table-body">
        <div class="custom-table-row" v-for="(operation, idx) in operations"  :key="idx" :class="idx % 2 == 0 ? 'dark': 'light'">
            <div class="custom-data-cell">
                <template v-if="layoutAsMobile">
                    <div class="percent-amount-block">
                        <div class="percent-mark" v-if="operation.percent">{{ (operation.percent * 100).toFixed(2) + '%'}}</div>
                        <p class="operation-amount">{{ operation.amount.toFixed(2) + ' dream'}}</p>
                    </div>
                    <p class="operation-name">{{ operation.name ?? '-' }}</p>
                </template>
                <template v-else>
                    <p class="operation-name">{{ operation.name  ?? '-' }}</p>
                    <div class="percent-mark" v-if="operation.percent">{{ (operation.percent * 100).toFixed(2) + '%'}}</div>
                </template>
            </div>
            <div class="custom-data-cell" v-if="!layoutAsMobile"><p class="operation-amount">{{ operation.amount.toFixed(2) + ' dream'}}</p></div>
            <div class="custom-data-cell"><p>{{ $dateFilter(operation.createdTimestamp, 'datetime') }}</p></div>
        </div>
    </div>
  </div>
  </div>
</template>

<script>
import TableOptions from "@/components/TableOptions.vue"
export default {
  props: [
    'operations', 'showOptions', 'showTable'
  ],
  components: {
    TableOptions
  },
  data: () => ({
    types: ['all','replenish','dividend'],
    type: 'all'
  }),
  methods: {
      selectOption(idx) {
        this.type = this.types[idx]
        this.$emit('changeStatus', this.type)
      }
  },
  computed: {
      layoutAsMobile() {
          return window.innerWidth <= 1024
      }
  }
}
</script>

