<template>
  <div class="operations">
      <p class="title">{{$localizeFilter('operations_on_account')}}</p>
      <OperationsTable 
      :showTable="operations.length > 0"
      :operations="operations" :showOptions="showOptions" @changeStatus='changeStatus'/>
      <EmptyTable v-if="operations.length == 0" :text="$localizeFilter('no_operations')"/>
      <pagination class="pag-fix" v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import OperationsTable from '@/components/OperationsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import localizeFilter from "@/filters/localize.filter"
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: ['type','showOptions'],
  components: {
    OperationsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch:{
      page() {
          this.$nextTick(this.loadData);
      },
  },
  setup() {
    
  },
  data: () => ({
    operations: [],
    count: 0,
    status: 'all'
  }),
  mounted() {
    this.loadData()
  },
  methods: {
    changeStatus(status) {
      this.status = status
      this.loadData()
    },
    async loadData() {
     if (this.type=='sigma') {
        try {
            const data = {skip: this.skip, limit: this.limit}
            this.operations = await this.$store.dispatch('getSigmaOperationsByFilter', data)
            const {count} = await this.$store.dispatch('getSigmaOperationsByFilterCount', data)
            this.count = count
            this.setupPagination(this.operations)
        } catch(e) {
            this.$error(e)
        } 
     } else {
        try {
            const data = {status: this.status, skip: this.skip, limit: this.limit}
            this.operations = await this.$store.dispatch('getBalanceOperationsByFilter', data)
            const {count} = await this.$store.dispatch('getBalanceOperationsByFilterCount', data)
            this.count = count
            this.setupPagination(this.operations)
        } catch(e) {
            this.$error(e)
        } 
     }
     }
  }
})
</script>

